  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.searchbox{
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 10px;
  width:350px;
  height: 20px;
}

.result-content{
  margin-top:50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.tbl-content{
  width:80%;

}

table, th, td {
  border:1px solid #ccc;
  border-collapse: collapse;
  padding: 5px;
}

th {
  text-align: left;
}

.searchinfo{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}


